@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Raleway';
}
.App
{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  padding:30px;
  flex:1.5 1;
  flex:var(--sidebar-flex) 1;
  background: rgb(255,102,102);
  background: linear-gradient(180deg, rgba(255,153,153,1) 50%,rgba(255,80,80,1)80%);
}
.condiv
{
  padding: 50px 100px;
  background: rgb(255,102,102);
  color: white;
  flex:6 1;
  flex:var(--condiv-flex) 1;
}

.home
{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(255,102,102);
  
}
.name{
  font-size:50px;
  padding:20px 10px 10px 10px;
  position: relative;
  top: 40px;
  color:white;
  
}
.vid1{
  display:flex;
  padding: 5px 5px 5px 5px;
  width:250px;
  height:250px;
  float: left;
  position: relative; 
  /* right: 100px; */
  top: 40px; 
 } 
.vid2{
  display:flex;
  padding:5px 5px 5px 5px;
  width:250px;
  height:250px;
  float: left;
  position: relative;
  top: 40px;
}
.vid3{
  display:flex;
  width: 250px;
  height: 250px;
  float: left;
  padding:5px 5px 5px 5px;
  position: relative;
  top: 40px;
  
}
.name :hover{
  color: rgb(255, 204, 102);
}
.profilepic
{
  /* border-radius:50%; */
  width:200px;
  border-style:solid;
  border-color:white;
  border-width:5px 5px 5px 5px;
  margin: 30px;
}
.title{
  position: relative;
  top: 20px;
}

nav ul{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style: none;
  text-decoration: none;
  color: white !important;
}
nav ul li
{
  margin:40px;
}
nav ul :hover{
  color: rgb(255, 255, 102);
}

a
{
  text-decoration: none;
  color: white;
}

.subtopic
{
  margin:10px 10px 30px 10px !important;
}
p{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.social
{
  position: relative;
  margin-top: 0px;
  display: flex;
  top: 60px;
}
.social a
{
  padding-right: 30px;
  font-size: 25px;
  color:white;
}
.social a:hover
{
  color: rgb(255, 204, 102);;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  .condiv
  {
    padding: 30px ;
  }
  nav ul
  {
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }
  .social i
  {
    padding-right: 0px;
  }
}

